.container {
  width: 120rem;
  max-width: 95%;
  margin: 0 auto;
}

.text-center {
  align-items: center;
}

.text-white {
  color: #FFFFFF;
}

.section {
  padding-bottom: 12.5rem;
}

.main__title,
.section__title {
  color: #2D3134;
  line-height: 1.4;
  font-weight: 700;
}

.main__title {
  font-size: 4.4rem;
  margin-bottom: 4rem;
}
.main__title span {
  color: #263FA4;
}
@media (max-width: 900px) {
  .main__title {
    font-size: 3.2rem;
  }
}

.section__title {
  font-size: 3.2rem;
  margin-bottom: 1.6rem;
  display: flex;
  align-items: center;
  gap: 2rem;
}
.section__title span {
  display: block;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background-color: #FF7B29;
}
@media (max-width: 900px) {
  .section__title {
    font-size: 2.4rem;
  }
}

.section__description {
  color: #676A6C;
}

.overlay {
  transition: 0.3s;
}

.overlay-active {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.4);
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
  overflow-x: hidden;
}
@media (max-width: 900px) {
  html {
    font-size: 56.25%;
  }
}
@media (max-width: 768px) {
  html {
    font-size: 50%;
  }
}

body {
  overflow-x: hidden;
  box-sizing: border-box;
  font-size: 1.4rem;
  font-weight: 300;
  font-family: "Poppins", sans-serif;
  line-height: 1.7;
  font-weight: 400;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  font-family: inherit;
  font-size: inherit;
}

select, input {
  font-family: inherit;
  font-size: inherit;
  background: none;
  border: none;
}

a {
  display: inline-block;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

ul, ol {
  padding: 0;
  list-style: none;
}

.swiper-slide {
  width: 30rem !important;
}
@media (max-width: 900px) {
  .swiper-slide {
    width: 25rem !important;
  }
}

.header {
  transition: all 0.3s ease;
}
.header__container {
  height: 10rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__name {
  font-weight: 600;
  font-size: 2.4rem;
}
.header__name span {
  color: #FF7B29;
}
.header__button {
  background-color: #FF7B29;
  color: #FFFFFF;
  padding: 1rem 2rem;
  border-radius: 10px;
  transition: 0.3s;
}
.header__button:hover {
  background-color: #f55e00;
}
@media (max-width: 900px) {
  .header__button {
    display: none;
  }
}
.header__menuButtonOpen {
  display: none;
  font-size: 4rem;
  color: #132742;
}
@media (max-width: 900px) {
  .header__menuButtonOpen {
    display: block;
  }
}
.header__menuButtonClose {
  display: none;
  font-size: 5rem;
  color: #132742;
  cursor: pointer;
}
@media (max-width: 900px) {
  .header__menuButtonClose {
    display: block;
  }
}

@media (max-width: 900px) {
  .navigation {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 50%;
    background-color: #fff;
    padding: 3rem;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    z-index: 100;
    transform: translate(100%);
    transition: all 0.3s ease;
  }
}
@media (max-width: 480px) {
  .navigation {
    width: 60%;
  }
}
.navigation__header {
  display: none;
}
@media (max-width: 900px) {
  .navigation__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5rem;
    border-bottom: 1px solid #ECECEC;
    padding-bottom: 1rem;
  }
}
.navigation__list {
  display: flex;
  gap: 3.2rem;
}
@media (max-width: 900px) {
  .navigation__list {
    flex-direction: column;
  }
}
.navigation__link {
  color: #6A6F77;
  transition: 0.3s;
  border-bottom: 2px solid transparent;
}
.navigation__link:hover {
  color: #2D3134;
  border-bottom: 2px solid #FF7B29;
}
@media (max-width: 900px) {
  .navigation__link {
    font-size: 1.8rem;
  }
}

.showMenu .navigation {
  transform: translate(0%);
}

.hero__container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0;
  place-items: center;
}
@media (max-width: 480px) {
  .hero__container {
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 6.4rem;
  }
}
.hero__text {
  grid-column: 1/3;
}
.hero__description {
  color: #676A6C;
  width: 85%;
  line-height: 2;
  margin-bottom: 3.2rem;
}
@media (max-width: 900px) {
  .hero__description {
    line-height: 1.8;
    width: 100%;
  }
}
.hero__buttons {
  display: flex;
  gap: 3.2rem;
}
.hero__button {
  background-color: #FF7B29;
  color: #FFFFFF;
  padding: 1rem 2rem;
  border-radius: 10px;
  transition: 0.3s;
}
.hero__button:hover {
  background-color: #f55e00;
}
.hero__watch {
  display: flex;
  align-items: center;
  gap: 1.6rem;
  background-color: #FFFFFF;
  color: #FF7B29;
  border: 1px solid #FF7B29;
  padding: 1rem 2rem;
  border-radius: 10px;
  transition: 0.3s;
}
.hero__watch:hover {
  background-color: #FF7B29;
  color: #FFFFFF;
}
.hero__imgBox {
  grid-column: 3/5;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 480px) {
  .hero__imgBox {
    justify-content: space-evenly;
  }
}
@media (max-width: 900px) {
  .hero__img {
    width: 80%;
  }
}
@media (max-width: 480px) {
  .hero__img {
    width: 70%;
  }
}
.hero__countdown {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}
@media (max-width: 480px) {
  .hero__countdown {
    width: 100%;
    flex-direction: row;
    justify-content: space-around;
  }
}
.hero__countdown-item {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.hero__countdown-title {
  color: #0A192D;
  font-size: 3rem;
  font-weight: 700;
}
@media (max-width: 900px) {
  .hero__countdown-title {
    font-size: 2rem;
  }
}
@media (max-width: 480px) {
  .hero__countdown-title {
    font-size: 1.6rem;
  }
}
.hero__countdown-label {
  color: #777F8B;
}
.hero__countdown-icon {
  font-size: 2.8rem;
  color: #FF7B29;
}

.search__container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 6.4rem;
  place-items: center;
  box-shadow: 0px 40px 100px rgba(110, 110, 110, 0.08);
  border-radius: 12px;
  padding: 2rem;
}
@media (max-width: 480px) {
  .search__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3.2rem;
    place-items: center;
  }
}
.search__field {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}
.search__title {
  font-weight: 600;
  font-size: 1.6rem;
  color: #132742;
}
.search__select {
  border: none;
  width: 100%;
}
.search__select option {
  color: #697280;
}
.search__button {
  margin-top: 3.2rem;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
  gap: 1rem;
  background-color: #132742;
  color: #FFFFFF;
  padding: 1rem 2rem;
  border-radius: 10px;
  transition: 0.3s;
}
.search__button:hover {
  background-color: #08101a;
}
@media (max-width: 480px) {
  .search__button {
    grid-column: 1/3;
    padding: 1.5rem 2rem;
  }
}

.products__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6.4rem;
}
@media (max-width: 900px) {
  .products__header {
    flex-direction: column;
    align-items: flex-start;
  }
}
@media (max-width: 900px) {
  .products__header-text {
    margin-bottom: 3.2rem;
  }
}
.products__header-buttons {
  display: flex;
  gap: 3.2rem;
  align-self: center;
}
.products__header-button {
  cursor: pointer;
  font-size: 2rem;
  border: 1px solid #ECECEC;
  color: #132742;
  padding: 1rem;
  border-radius: 50%;
  width: 5rem;
  height: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
}
.products__header-button:hover {
  background-color: #FF7B29;
  color: #FFFFFF;
}
.products__card {
  padding: 1rem;
  border: 1px solid #ECECEC;
  border-radius: 12px;
}
.products__card-content {
  margin-top: 1.6rem;
}
.products__card-title {
  font-size: 1.8rem;
  font-weight: 600;
}
.products__card-year {
  font-size: 1.2rem;
  font-weight: 300;
  color: #8A8A8A;
  margin-bottom: 3rem;
}
.products__card-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4rem;
}
.products__card-price {
  font-size: 2rem;
  font-weight: 600;
}
@media (max-width: 900px) {
  .products__card-price {
    font-size: 1.8rem;
  }
}
.products__card-button {
  background-color: #FFFFFF;
  color: #31C3E0;
  border: 1px solid #31C3E0;
  padding: 1rem 2rem;
  border-radius: 10px;
  transition: 0.3s;
  padding: 0.75rem 1rem;
}
.products__card-button:hover {
  background-color: #31C3E0;
  color: #FFFFFF;
}
.products__card-extra {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.products__card-bids, .products__card-bidding {
  font-size: 1.2rem;
}
.products__card-bids {
  font-weight: 600;
  color: #2D3134;
}
.products__card-bidding {
  font-weight: 300;
  color: #676A6C;
}

.gallery__header {
  margin-bottom: 6.4rem;
}
.gallery__grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 3rem;
  place-items: center;
}
@media (max-width: 900px) {
  .gallery__grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 1rem;
    place-items: center;
  }
}
.gallery__item {
  border-radius: 15px;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: 0.3s;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
}
.gallery__item-number {
  text-align: center;
  width: 6rem;
  padding: 0.5rem;
  background-color: #fff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
  border-radius: 14px;
  color: #FF7B29;
  font-weight: 600;
}
.gallery__item-name {
  font-size: 2rem;
  font-weight: 300;
  color: #FFFFFF;
}
.gallery__item-name h3 {
  text-shadow: 0px 6px 15px rgba(0, 0, 0, 0.2);
}
.gallery__item-name p {
  font-size: 1.2rem;
  opacity: 0.9;
}
@media (max-width: 900px) {
  .gallery__item-name {
    font-size: 1.6rem;
  }
}
.gallery__item:nth-child(1) {
  grid-column: 1/4;
  height: 25rem;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(36, 36, 40, 0.3) 100%), url("../../../build/assets/img/gallery-image1.png");
}
.gallery__item:nth-child(2) {
  grid-column: 4/8;
  grid-row: 1/3;
  height: 100%;
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(36, 36, 40, 0.3) 100%), url("../../../build/assets/img/gallery-image2.png");
}
.gallery__item:nth-child(3) {
  grid-column: 1/4;
  grid-row: 2/3;
  height: 25rem;
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(36, 36, 40, 0.3) 100%), url("../../../build/assets/img/gallery-image3.png");
}
.gallery__item:nth-child(4) {
  grid-column: 8/10;
  grid-row: 2/3;
  height: 25rem;
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(36, 36, 40, 0.3) 100%), url("../../../build/assets/img/gallery-image4.png");
}
.gallery__item:nth-child(5) {
  grid-column: 10/13;
  grid-row: 2/3;
  height: 25rem;
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(36, 36, 40, 0.3) 100%), url("../../../build/assets/img/gallery-image5.png");
}
.gallery__item:nth-child(6) {
  grid-column: 8/13;
  grid-row: 1/2;
  height: 25rem;
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(36, 36, 40, 0.3) 100%), url("../../../build/assets/img/gallery-image6.png");
}
@media (max-width: 480px) {
  .gallery__item:nth-child(1) {
    grid-column: 1/7;
    background-position: right;
  }
  .gallery__item:nth-child(2) {
    grid-column: 7/13;
    grid-row: 1/2;
  }
  .gallery__item:nth-child(3) {
    grid-column: 1/7;
    grid-row: 2/3;
  }
  .gallery__item:nth-child(4) {
    grid-column: 7/13;
    grid-row: 2/3;
  }
  .gallery__item:nth-child(5) {
    grid-column: 1/7;
    grid-row: 3/4;
  }
  .gallery__item:nth-child(6) {
    grid-column: 7/13;
    grid-row: 3/4;
  }
}
.gallery__item:hover {
  transform: scale(1.01);
}

.newsletter__container {
  background-color: #132742;
  border-radius: 20px;
  display: grid;
  grid-template-columns: 3fr 1fr;
  padding: 8rem 8rem 1rem 8rem;
}
@media (max-width: 480px) {
  .newsletter__container {
    grid-template-columns: 1fr;
    place-items: center;
    gap: 3.2rem;
    padding: 2rem;
  }
}
.newsletter__content {
  color: #FFFFFF;
}
.newsletter__title {
  font-size: 3.8rem;
  font-weight: 600;
}
@media (max-width: 900px) {
  .newsletter__title {
    font-size: 2.4rem;
  }
}
.newsletter__text {
  font-size: 1.6rem;
  margin-bottom: 4.8rem;
}
@media (max-width: 900px) {
  .newsletter__text {
    font-size: 1.4rem;
  }
}
.newsletter__form {
  background-color: #FFFFFF;
  width: 45rem;
  max-width: 95%;
  display: grid;
  grid-template-columns: 6fr 1fr;
  padding: 1rem;
  gap: 1.2rem;
  border-radius: 10px;
}
@media (max-width: 480px) {
  .newsletter__form {
    width: 100%;
    gap: 0;
  }
}
.newsletter__button {
  padding: 1rem;
  border-radius: 5px;
  background-color: #FF7B29;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.newsletter__imageBox {
  margin-top: -25rem;
}
@media (max-width: 480px) {
  .newsletter__imageBox {
    width: 20%;
    margin-top: 0;
    grid-row: 2/3;
  }
}

.testimonials__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3.2rem;
  place-items: center;
}
@media (max-width: 480px) {
  .testimonials__container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 3.2rem;
    place-items: center;
  }
}
.testimonials__title {
  font-size: 3.2rem;
  margin-bottom: 2rem;
}
@media (max-width: 900px) {
  .testimonials__title {
    font-size: 2.4rem;
  }
}
@media (max-width: 480px) {
  .testimonials__imageBox {
    width: 70%;
  }
}
.testimonials__text {
  font-size: 1.8rem;
  font-style: italic;
  color: #676A6C;
  margin-bottom: 4.8rem;
}
@media (max-width: 900px) {
  .testimonials__text {
    font-size: 1.6rem;
  }
}
@media (max-width: 480px) {
  .testimonials__text {
    font-size: 1.4rem;
  }
}
.testimonials__user {
  display: flex;
  gap: 1.6rem;
}
@media (max-width: 480px) {
  .testimonials__user {
    justify-content: center;
  }
}
.testimonials__user-name {
  font-size: 1.2rem;
  font-weight: 700;
}
.testimonials__user-profession {
  font-size: 1.2rem;
  font-weight: 300;
  margin-bottom: 1.2rem;
}

.footer__container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.2rem;
  place-items: center;
  place-items: start;
}
@media (max-width: 480px) {
  .footer__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3.2rem;
    place-items: center;
    place-items: unset;
  }
}
.footer__subtitle {
  color: #020000;
  font-weight: 600;
  margin-bottom: 3.2rem;
}
.footer__list {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}
.footer__link {
  color: #676A6C;
  transition: 0.3s;
}
.footer__link:hover {
  color: #2D3134;
}
.footer__text {
  line-height: 1.7;
  color: #676A6C;
  margin-bottom: 1.6rem;
}
.footer__email {
  color: #30BBD7;
  font-weight: 700;
}
.footer__navigation {
  padding-bottom: 5rem;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 480px) {
  .footer__navigation {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3.2rem;
  }
}
.footer__social {
  display: flex;
  gap: 1.6rem;
  font-size: 2.4rem;
  color: #6A6F77;
}
.footer__social i {
  transition: 0.3s;
  cursor: pointer;
}
.footer__social i:hover {
  color: #2D3134;
}